import BaseApiService from "./BaseApiService";
const DEFAULT_PATH = "/custom/api/applicant/search";

const DELETE_APPLICANT_PATH = "/custom/api/applicant/inactivate";

class ApplicantService extends BaseApiService {
  getAll = (start = 0, limit = 10, search: string) => {
    const endPoint = `${DEFAULT_PATH}?${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getApplicantInfo(start = 0, limit = 10, applicantType: string, loanUid: string) {
    const url = `${DEFAULT_PATH}?start=${start}&applicantType=${applicantType}&loan.uid=${loanUid}&limit=${limit}&isActive=true`;
    return this.makeGetRequestWithAuth(url);
  }

  getApplicantListForQc(applicantType: string, loanUid: string) {
    const url = `${DEFAULT_PATH}?applicantType:in=${applicantType}&loan.uid=${loanUid}&isActive=true`;
    return this.makeGetRequestWithAuth(url);
  }

  // ----------------------- To delete the applicant --------------------------- //
  deleteApplicantInfo(id: string) {
    const endPoint = DELETE_APPLICANT_PATH + `/${id}`;
    return this.makePutRequestWithAuth(endPoint, {});
  }

  //---------------------------------------------------------//
  getCustomerName = (start = 0, limit = 10, search: string) => {
    const endPoint = `${DEFAULT_PATH}?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new ApplicantService();
